<template>
	<div class="login-container">
		<div class="login-header">
			<div class="login-zhong">
				<div class="login-quxiao">
					<img src="../assets/fanhui-fense.png" @click="back()">
				</div>
				<div class="login-ti">登录</div>
				<div></div>
			</div>
		</div>
		<div class="login-content">
			<div class="login-baise">
				<div class="login-flex">
				<div class="login-shouji">
					<img src="../assets/icon_shouji.png">
				</div>
				<div class="login-haoma">
					<!--<input placeholder="请输入原手机号" type="text">-->
					<input placeholder="请输入原手机号" id="phone" type="" name="phone" v-model="phone">
				</div>
			</div>
			<div class="login-border-b"></div>
			<div class="login-between">
				<div class="login-flex">
					<div class="login-huoqu">
						<img src="../assets/icon_mima.png">
					</div>
					<div class="login-yzm">
						<!--<input placeholder="请输入验证码" type="text">-->
						<input type="password" name="pwd" placeholder="请输入验证码" v-model="code">
					</div>
				</div>
				<div>
					<div class="getCode">
						<!--<input :value="yzm_params.code_message" type="button" @click="get_yzm" v-show="yzm_params.is_send" />
				<input :value="yzm_params.code_message" type="button" v-show="!yzm_params.is_send" disabled="disabled" />-->
						<span v-show="show" @click="get_yzm">获取验证码</span>
						<span v-show="!show" class="count">{{count}}s后重新获取</span>
					</div>
				</div>
			</div>			
				<!--<div>
					<div class="login-yanzheng">获取验证码</div>
				</div>-->
			</div>
			<div class="login-border-b"></div>
			<div class="login-denglu" @click="login()">登录</div>
			<div class="login-huozhe">或使用以下方式登录</div>
			<div class="login-around">
				<div @click="thirdloginaa()">
					<div class="login-tu">
						<img src="../assets/btn_qq.png">
					</div>
					<div class="login-qq">QQ</div>
				</div>
				<div @click="thirdlogin()">
					<div class="login-tu">
						<img src="../assets/btn_wx.png">
					</div>
					<div class="login-wx">微信</div>
				</div>
			</div>
		</div>
		<div class="login-footer">

		</div>
	</div>
</template>

<script>
	import Qs from "qs"
	export default {
		name: 'HelloWorld',
		data() {
			return {
				msg: 'Welcome to Your Vue.js App',
				phone: '',
//				password: '',
				show: true,
				count: '',
				timer: null,
				code: "",
				wxid: '',
				qqid: '',
				yzm_params: {
					code_message: '获取验证码',
					is_send: true,
					time: 60,
					interval: ''
				}
			}
		},
		created: function() {
			// if (!!window.localStorage.getItem('login_time')) {

			// }else{
			// 	window.localStorage.setItem('login_time', new Date().getTime());
			// }

			var login_time = window.localStorage.getItem('login_time');
			if (!!login_time) {
				var this_time = new Date().getTime();
				var time = parseInt((Number(this_time) - Number(login_time))/86400000,10);
				if (!!window.localStorage.getItem('user_info') && time < 30) {
					this.$router.push('/home');
				}else{

				}
			}else{

			}

			// window.localStorage.setItem('login_time', new Date().getTime());

		},
		//		mounted: function(){
		//			
		//		},
		methods: {
//			获取验证码
			get_yzm() {

				var that = this;
				var flg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;
				var asd = flg.test(that.phone)
				if(!asd) {
					alert("请输入正确的手机号");
					return;
				}
				var obj = {
					phone: that.phone
				};

				this.getCode()
				that.$http.post('/user/sendcode', Qs.stringify(obj), {
					headers: {
						"Content-Type": "application/x-www-form-urlencoded"
					}
				}).then((res) => {
					console.log(res)
					that.getCode();
				})

			},
			back() {
				window.history.back();
			},
			clear_phone() {
				this.phone = '';
			},
//			clear_password() {
//				this.password = '';
//			},
		//验证码
			getCode() {
				const TIME_COUNT = 60;
				if(!this.timer) {
					this.count = TIME_COUNT;
					this.show = false;
					this.timer = setInterval(() => {
						if(this.count > 0 && this.count <= TIME_COUNT) {
							this.count--;
						} else {
							this.show = true;
							clearInterval(this.timer);
							this.timer = null;
						}
					}, 1000)
				}
			},
			login() {
				var that = this;

				//				that.$router.push('/');
				var flg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/;
				var asd = flg.test(that.phone)
				if(!asd) {
					that.$alert("请输入正确的手机号",'提示');
					return;
				}
//				var mima = /^[a-zA-Z]\w{5,17}$/;
//				var yan = mima.test(that.password)
//				if(!yan) {
//					alert("请输入以字母开头，长度在6~18之间，只能包含字母、数字和下划线");
//					return;
//				}
				if(that.code == "") {
					// alert("请输入验证码");
					that.$alert('请输入验证码', '提示');
					return;
				}
				// console.log(that.phone)
				// console.log(that.password)
				// console.log(that.code)
				var params = {
					phone: that.phone,
//					password: that.password,
					code: that.code
				};
				that.$http.post('/user/login', Qs.stringify(params), {
					headers: {
						"Content-Type": "application/x-www-form-urlencoded"
					}
				}).then((res) => {
					// console.log(res);
					if(res.meta.code == 200) {
						window.localStorage.setItem('user_info', JSON.stringify(res.data));
						window.localStorage.setItem('login_time', new Date().getTime());
						
						that.$router.push('/home');
					}else{
						that.$alert(res.meta.msg, '提示');
					}
				})

			},
			//QQ登录
			thirdloginaa:function(){ 
				var that = this;
				dsBridge.call("third_party_login","2");
	      		dsBridge.register("third_msg", function(r) {
//					var that = this
					var params = {
					qqid: r,
					};
					that.$http.post('/user/thirdlogin', Qs.stringify(params), {
						headers: {
							"Content-Type": "application/x-www-form-urlencoded"
						}
					}).then((res) => {
						// console.log(res);
						if(res.meta.code == 200) {
//							console.log(res)
							window.localStorage.setItem('user_info', JSON.stringify(res.data));
							window.localStorage.setItem('login_time', new Date().getTime());
							
							that.$router.push('/home');
						}else{
							alert( res.meta.msg )
						}
					})
					          
		
       			})
			},
			//微信登录
			thirdlogin() { 
				var that = this;
				dsBridge.call("third_party_login","1");
	      		dsBridge.register("third_msg", function(r) {
//					var that = this
					var params = {
						wxid: r,
					};
					that.$http.post('/user/thirdlogin', Qs.stringify(params), {
						headers: {
							"Content-Type": "application/x-www-form-urlencoded"
						}
					}).then((res) => {
						// console.log(res);
						if(res.meta.code == 200) {
//							console.log(res)、
							window.localStorage.setItem('user_info', JSON.stringify(res.data));
							window.localStorage.setItem('login_time', new Date().getTime());
							
							that.$router.push('/home');
						}else{
							alert( res.meta.msg )
						}
					})
					          
		
       			})
			}
		}
	}
</script>

<style scoped>
	.login-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
		background: url('../assets/beijing.png');
		background-size: 100% 100%;
		background-repeat: no-repeat;
		/*margin-top: -5rem;*/
	}
	
	.login-header {
		height: 50px;
		/*background: #FFFFFF;*/
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.login-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.login-quxiao img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.login-ti {
		font-size: 1.8rem;
		color: #fd5997;
		margin-left: -1.5rem;
	}
	
	.login-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	.login-baise{
		background: hsla(0,0%,100%,.7);
		width: 90%;
		margin: 0 auto;
		margin-top: 2rem;
	}
	.login-flex {
		display: flex;
	}
	
	.login-shouji img {
		width: auto;
		height: 2rem;
		margin: 2rem 1.5rem 1.1rem 3rem;
	}
	
	.login-haoma input {
		border: none;
		outline: none;
		background-color: transparent;
		font-size: 1.5rem;
		color: #adadad;
		/*padding-left: 1rem;*/
		margin-top: 2rem;
	}
	
	.login-border-b {
		border-bottom: 1px solid #EAEAEA;
		margin: 0 3rem;
	}
	
	.login-between {
		display: flex;
		justify-content: space-between;
	}
	
	.login-huoqu img {
		width: auto;
		height: 2rem;
		margin: 2rem 1.5rem 1.1rem 3rem;
	}
	
	.login-yzm {
		width: 6rem;
	}
	
	.login-yzm input {
		border: none;
		outline: none;
		background-color: transparent;
		font-size: 1.5rem;
		color: #adadad;
		/*padding-left: 1rem;*/
		margin-top: 2rem;
	}
	
	.getCode {
		font-size: 1.2rem;
		color: #8CADF5;
		border: 1px solid #8CADF5;
		border-radius: 0.5rem;
		padding: 0.5rem 0.4rem;
		margin-top: 1.8rem;
		margin-right: 3.5rem;
		margin-bottom: 1.5rem;
	}
	
	.login-denglu {
		font-size: 1.8rem;
		color: #FFFFFF;
		border: 1px solid #fd5997;
		border-radius: 25px;
		background: #fd5997;
		text-align: center;
		padding: 0.5rem 0;
		width: 90%;
		margin: 0 auto;
		margin-top: 6rem;
	}
	
	.login-huozhe {
		font-size: 1.5rem;
		color: #999999;
		text-align: center;
		padding-top: 5rem;
	}
	
	.login-around {
		display: flex;
		justify-content: space-around;
	}
	
	.login-tu img {
		width: auto;
		height: 3rem;
		margin-top: 3rem;
	}
	
	.login-qq {
		font-size: 1.5rem;
		color: #8CADF5;
		padding-left: 0.5rem;
	}
	
	.login-wx {
		font-size: 1.5rem;
		color: #68d617;
		padding-left: 0.2rem;
	}
</style>


